/* eslint-disable @next/next/no-img-element */
"use client"
import React, { useEffect, useState } from 'react';
import axios from 'axios';
import Laptop from '@/MyTypes/laptob';
import ElementShowLapTobHomeScrean from './ElementShowLapTobHomeScrean';
import Slider from 'react-slick';
import 'slick-carousel/slick/slick.css';
import 'slick-carousel/slick/slick-theme.css'
import LaptopSlider from './SliderLaptob';


const LaptopList: React.FC = () => {
  const [laptops, setLaptops] = useState<Laptop[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState('');

  useEffect(() => {
    axios.get('http://localhost:5000/api/laptops')
      .then(response => {
        setLaptops(response.data);
        setLoading(false);
      })
      .catch(error => {
        console.error('Error fetching laptops:', error);
        setError('Error fetching laptops');
        setLoading(false);
      });
  }, []);

  if (loading) {
    return <p>Loading...</p>;
  }

  if (error) {
    return <p>{error}</p>;
  }

  const getRandomLaptops = (laptops: Laptop[], count: number): Laptop[] => {
    const shuffled = laptops.sort(() => 0.5 - Math.random());
    return shuffled.slice(0, count);
  };
  
  const randomLaptops:Laptop[] = getRandomLaptops(laptops, 6);


  return (
    <div className='bg-white mt-2 mx-10 rounded-2xl border-2'>
      <br />
      <div className='text-2xl mx-5'>
        <span className='font-bold mr-3 text-gray-900'>لب تاپ در تکنولوژی</span>
        <br />
      </div>
      <div className='flex mt-2'>
      return (
    <div>
      <h1>لپ‌تاپ‌های ما</h1>
      <LaptopSlider laptops={laptops} />
    </div>
  );
      </div>
    </div>
  );
};

export default LaptopList;
